import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import "./AuthenticationModal.module.css";
import Modal from "../../UI/Modal";

function AuthenticationModal({ onClose, loginId }) {
  const navigate = useNavigate();

  const [authNum, setAuthNum] = useState("");

  const handleSubmit = async (event) => {
    event.preventDefault();

    const formData = new FormData();
    formData.append("authCode", authNum);
    formData.append("loginId", loginId);

    try {
      const response = await fetch("http://localhost:8080/auth/auth-num", {
        method: "POST",
        body: formData,
      });

      if (!response.ok) {
        alert(" .");
        return;
      }

      const joinResponse = await fetch(
        "http://localhost:8080/auth/join-confirm",
        {
          method: "POST",
          body: formData,
        }
      );

      if (!joinResponse.ok) {
        alert(
          "   .\n ,   ."
        );
        return;
      }

      alert(" .");
      navigate("/login");
    } catch (error) {
      alert("    .");
    }
  };

  return (
    <Modal
      title={" "}
      modalClass="modal"
      bodyClass="modal-body"
      onClose={onClose}
    >
      <div className="content">
        <div className="card">
          <div className="form-container">
            <form className="authentication-form" onSubmit={handleSubmit}>
              <div className="description-container">
                <p className="description">
                       .
                </p>
                <p className="description">
                    6    .
                </p>
              </div>

              <div className="form-field">
                <span className="user-email" id="loginEmail">
                  {loginId}
                </span>
                <div className="input-container">
                  <input
                    className="input"
                    type="text"
                    id="userInput"
                    placeholder="  "
                    value={authNum}
                    onChange={(e) => setAuthNum(e.target.value)}
                  />
                </div>
              </div>
            </form>
          </div>
          <div className="button-section">
            <button className="before-button" onClick={() => navigate(-1)}>
              
            </button>
            <button
              className="next-button"
              id="next-button"
              onClick={handleSubmit}
            >
              
            </button>
          </div>
        </div>
      </div>
    </Modal>
  );
}

export default AuthenticationModal;
