import React, { useState, useEffect, useTransition } from "react";
import { ChevronLeft, PencilSquare } from "react-bootstrap-icons";
import { useNavigate } from "react-router-dom";
import "./EditPage.css";
import { profile } from "../../assets/images";

const EditPage = () => {
  const navigate = useNavigate();
  const [userInfo, setUserInfo] = useState({
    email: "",
    id: "",
    password: "",
    passwordConfirm: "",
    phoneNumber: "",
    name: "",
    profileImage: null,
  });
  const [passwordError, setPasswordError] = useState("");
  const [passwordMatchError, setPasswordMatchError] = useState("");
  const [fetchState, setFetchState] = useState({ accessToken: null });

  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const response = await fetch(
          "http://138.2.122.249:8080/v1/api/myInfo",
          {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${fetchState.accessToken}`,
            },
          }
        );
        if (response.ok) {
          const userData = await response.json();
          setUserInfo({
            email: userData.email,
            id: userData.id,
            phoneNumber: userData.phoneNumber,
            name: userData.name,
            profileImage: userData.profileImage,
          });
        } else {
          throw new Error("    .");
        }
      } catch (error) {
        console.error("     :", error);
      }
    };

    fetchUserData();
  }, []);

  const editBackButtonHandler = () => {
    navigate("/mypage");
  };

  const passwordChangeHandle = (e) => {
    const password = e.target.value;
    setUserInfo({
      ...userInfo,
      password: password,
    });

    if (password === "") {
      setPasswordError("");
    } else if (
      !password.match(
        /^(?=.*[a-zA-Z])(?=.*\d)(?=.*[$`~!@$!%*#^?&()\-_=+])[a-zA-Z\d$`~!@$!%*#^?&()\-_=+]{8,20}$/
      )
    ) {
      setPasswordError(", ,   8  .");
    } else {
      setPasswordError("");
    }
  };

  const passwordConfirmChangeHandle = (e) => {
    const passwordConfirm = e.target.value;
    setUserInfo({
      ...userInfo,
      passwordConfirm: passwordConfirm,
    });

    if (passwordConfirm === "") {
      setPasswordMatchError("");
    } else if (passwordConfirm !== userInfo.password) {
      setPasswordMatchError("  .");
    } else {
      setPasswordMatchError("");
    }
  };

  const handleFileUpload = (e) => {
    const file = e.target.files[0];
    const reader = new FileReader();

    reader.onloadend = () => {
      setUserInfo({
        ...userInfo,
        profileImage: reader.result,
      });
    };

    if (file) {
      reader.readAsDataURL(file);
    }
  };

  const handleSubmit = async () => {
    if (userInfo.password !== userInfo.passwordConfirm) {
      alert("  .  ");
      setUserInfo({
        ...userInfo,
        password: "",
        passwordConfirm: "",
      });
      setPasswordError("");
      setPasswordMatchError("");
      return;
    }
    if (
      !userInfo.password.match(
        /^(?=.*[a-zA-Z])(?=.*\d)(?=.*[$`~!@$!%*#^?&()\-_=+])[a-zA-Z\d$`~!@$!%*#^?&()\-_=+]{8,20}$/
      )
    ) {
      alert(
        " , ,   8  ."
      );
      setUserInfo({
        ...userInfo,
        password: "",
        passwordConfirm: "",
      });
      setPasswordError("");
      setPasswordMatchError("");
      return;
    }

    try {
      const response = await fetch(
        "http://138.2.122.249:8080/v1/api/updatePassword",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            password: userInfo.password,
          }),
        }
      );
      if (response.ok) {
        alert(" .");
        navigate("/");
      } else {
        console.error("  ");
      }
    } catch (error) {
      console.error("  :", error);
    }
  };

  return (
    <div className="editBox">
      <div className="editBackButton">
        <button className="editPageBack" onClick={editBackButtonHandler}>
          <ChevronLeft />   
        </button>
      </div>
      <div className="editSection">
        <div className="editLeftSection">
          <div className="editProfile">
            <img src={userInfo.profileImage || profile} alt="profile" />
            <label htmlFor="fileUpload">
              <PencilSquare className="editProfileIcon" />
            </label>
            <input
              id="fileUpload"
              type="file"
              accept="image/*"
              style={{ display: "none" }}
              onChange={handleFileUpload}
            />
          </div>
        </div>
        <div className="editRightSection">
          <div>
            <div className="userInfo">
              <label className="textInfo"></label>
              <input
                className="textBox"
                type="email"
                value={userInfo.email}
                readOnly
              ></input>
            </div>
            <div className="userInfo">
              <label className="textInfo"> </label>
              <input
                className="textBox"
                type="text"
                value={userInfo.id}
                readOnly
              ></input>
            </div>
            <div className="userInfo">
              <label className="textInfo"> </label>
              <input
                className="editTextBox"
                type="password"
                value={userInfo.password}
                onChange={passwordChangeHandle}
              ></input>
              {passwordError && <span className="error">{passwordError}</span>}
            </div>
            <div className="userInfo">
              <label className="textInfo"> </label>
              <input
                className="editTextBox"
                type="password"
                value={userInfo.passwordConfirm}
                onChange={passwordConfirmChangeHandle}
              ></input>
              {passwordMatchError && (
                <span className="error">{passwordMatchError}</span>
              )}
            </div>
            <div className="userInfo">
              <label className="textInfo"></label>
              <input
                className="textBox"
                type="text"
                value={userInfo.phoneNumber}
                readOnly
              ></input>
            </div>
            <div className="userInfo">
              <label className="textInfo"></label>
              <input
                className="textBox"
                type="text"
                value={userInfo.name}
                readOnly
              ></input>
            </div>
            <div className="editButtonBox">
              <button className="editButton" onClick={handleSubmit}>
                
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default EditPage;
