import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import "./FoundPassword.css";
import axios from "axios";
import { leftArrow } from "../../assets/images";

const FoundPassword = () => {
  const navigate = useNavigate();
  const [id, setId] = useState("");
  const [email, setEmail] = useState("");
  const [isEmailValid, setIsEmailValid] = useState(false);
  const [message, setMessage] = useState("");

  const handleEmailValidation = async () => {
    try {
      await axios.post("/api/send-email", { email });
      setIsEmailValid(true);
      setMessage(" .");
    } catch (error) {
      console.error("  :", error);
      setIsEmailValid(false);
      setMessage("   .");
    }
  };

  const handlePasswordChangePage = () => {
    if (isEmailValid) {
      //       .
      navigate("/change-password");
    } else {
      alert("  .");
    }
  };

  return (
    <div className="container">
      <div className="fp-box">
        <div className="fp-h1">
          <img
            className="leftArrow"
            src={leftArrow}
            alt=""
            onClick={() => navigate("/login")}
          />
          <label> </label>
        </div>
        <form>
          <div>
            <input
              className="pf-id"
              type="text"
              placeholder=""
              value={id}
              onChange={(e) => setId(e.target.value)}
            />
          </div>
          <div>
            <input
              className="pf-email"
              type="email"
              placeholder=""
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
            <button
              className="pf-check"
              type="button"
              onClick={handleEmailValidation}
            >
              
            </button>
            {isEmailValid && (
              <p className={isEmailValid ? "success-message" : "error-message"}>
                 .
              </p>
            )}
          </div>
        </form>
        <p>{message}</p>
        <button
          className="pf-bt"
          type="button"
          onClick={handlePasswordChangePage}
        >
           
        </button>
      </div>
    </div>
  );
};

export default FoundPassword;
