import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import "./LoginAdd.css";
import { leftArrow } from "../../assets/images";
import AuthenticationModal from "./Modal/AuthenticationModal";

function LoginAdd() {
  const navigate = useNavigate();

  const [loginId, setLoginId] = useState("");
  const [email, setEmail] = useState("");
  const [memberName, setMemberName] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [phoneNum, setPhoneNum] = useState("");
  const [message, setMessage] = useState("");

  const [showModal, setShowModal] = useState();

  const validateInput = () => {
    //   
    let idCheck = /^[a-z]+[a-z0-9]{5,19}$/g;
    if (!idCheck.test(loginId)) {
      alert("   6~20    .");
      return false;
    }

    //   
    if (email === "") {
      alert(" .");
      return false;
    }

    //   
    const pwdCheck = /^(?=.*[a-zA-Z])(?=.*[!@#$%^*+=-])(?=.*[0-9]).{8,25}$/;
    if (!pwdCheck.test(password)) {
      alert(
        " , ,   8  ."
      );
      return false;
    }

    //   
    if (password !== confirmPassword) {
      alert(" .  .");
      return false;
    }

    //    
    const korean = /^[-]+$/;
    if (!korean.test(memberName)) {
      alert("    .");
      return false;
    }

    //   
    const tel = /^01([0|1|6|7|8|9])-?([0-9]{3,4})-?([0-9]{4})$/;
    if (!tel.test(phoneNum)) {
      alert(" 11  .");
      return false;
    }
    return true;
  };

  const requestSignUp = async (formData) => {
    const response = await fetch(
      "http://138.2.122.249:8080/auth/join?loginId=string&password=string&memberName=string&phoneNum=string",
      {
        method: "POST",
        body: formData,
      }
    );
    if (!response.ok) {
      throw new Error("Network response was not ok");
    }
    return response;
  };

  const initSignUpList = async (event) => {
    event.preventDefault();
    if (!validateInput()) return;

    const fullEmail = `${loginId}@${email}`;
    const formData = new FormData();

    formData.append("loginId", fullEmail);
    formData.append("email", email);
    formData.append("memberName", memberName);
    formData.append("password", password);
    formData.append("phoneNum", phoneNum);

    try {
      await requestSignUp(formData);
    } catch (error) {
      alert("     ");
    }
    setShowModal(true);
  };

  return (
    <div className="container">
      <div className="Add-box">
        <form onSubmit={initSignUpList}>
          <div className="add-h1">
            <img
              src={leftArrow}
              alt=""
              className="leftArrow"
              onClick={() => navigate("/login")}
            />
            <label></label>
          </div>
          <div className="row-container">
            <input
              className="login"
              type="text"
              placeholder=""
              value={loginId}
              onChange={(e) => setLoginId(e.target.value)}
            />
            <span className="at">@</span>
            <input
              className="domain"
              type="text"
              name="userEmail"
              placeholder="domain.co.kr"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
          </div>
          <div className="login-admin">
            <input
              className="Add"
              type="text"
              placeholder=""
              value={memberName}
              onChange={(e) => setMemberName(e.target.value)}
            />
            <input
              className="Add"
              type="password"
              placeholder=""
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
            <input
              className="Add"
              type="password"
              placeholder=" "
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
            />
            <input
              className="Add"
              type="text"
              placeholder=" "
              pwd
              value={phoneNum}
              onChange={(e) => setPhoneNum(e.target.value)}
            />
          </div>
          <button type="submit" className="Add-up">
            
          </button>
        </form>
        {message && <div className="message">{message}</div>}
        {showModal && (
          <AuthenticationModal
            loginId={`${loginId}@${email}`}
            onClose={() => setShowModal(false)}
          />
        )}
      </div>
    </div>
  );
}

export default LoginAdd;
