import React from "react";
import * as icons from "../../../assets/icons";

function Icon({ type, className }) {
  //      type className  props   

  const iconStyle = {
    display: "block",
    color: "transparent", //      iconstyle   color: "transparent"    
    background: `url(${icons[type]}) no-repeat center / contain`, //      no-repeat center / contain    ,  
    //          
  };

  return (
    <span>
      <i
        style={iconStyle}
        className={className} //      className prop         <i> 
      ></i>
    </span>
  );
}

export default Icon;
